.sliderButton {
  display: flex;
  position: absolute;
  z-index: 2;
  top: 50%;
  cursor: pointer;

  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border-radius: 20%;
  opacity: 0.6;
  border: 1px solid rgba(#000, 0.1);
  transform: translateY(-50%);
}
.Left {
  left: 15px;
}

.Right {
  right: 15px;
}
